// Null style
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

a {
  text-decoration: none;
}

li {
  list-style: none;
}


.container {
  width: 1105px;
  margin: 0 auto;
}

.title{
  color: #2D2D2F;
  font-size: 46px;
  font-weight: 900;
  margin-bottom: 47px;
  text-align: center;
  & span{
    font-weight: 500;
  }
}





