.calc {
  display: flex;
  flex-direction: column;
  align-items: end;
  margin-right: 50px;

  &__btn {
    display: flex;
    align-items: center;
    background-color: #84b73d;
    padding: 13px 26px;
    color: #fff;
    border: none;
    border-radius: 4px;
    margin-bottom: 13px;

    &::after {
      content: "";
      display: flex;
      width: 16px;
      height: 19px;
      background: url("../../../assets/img/calc.svg");
      margin-left: 10px;
    }
  }

  &__link {
    display: flex;
    align-items: center;
    color: #96989f;
    font-size: 11px;
    font-weight: 600;

    &::before {
      content: "";
      display: block;
      width: 10px;
      height: 7px;
      background: url("../../../assets/img/letter.svg");
      margin-right: 10px;
    }
  }
}
